.tableGreen {
  background-color: #bcd0c7;
}
.tableBlue {
  background-color: #cff4fc;
}

#departments,
#events {
  width: 60rem;
  font-size: 4rem;
  height: 5rem;
}
.selectors {
  display: flex;
  justify-content: space-between;
}

*:focus {
  outline: none;
}

.admin-table {
  font-size: 2rem;
  color: white;
  color: var(--grey);
}

.title {
  padding: 5rem;
  text-align: center;
}

.title h2 {
  font-size: 3rem;
}
